.category_topBanner {
  margin: 0 auto 58px;
  width: 1200px;
}

.category_topBanner .swiper-slide {
  height: 146px;
  border-radius: 10px;
  overflow: hidden;
}

.category_topBanner .swiper-slide a {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.2);
}

.category_hot {
  margin-bottom: 50px;
  padding: 90px calc(50% - 600px) 140px;
  background-color: #f7f7f7;
}

.category_hot h4 {
  font-size: 30px;
  color: #000;
}

.category_hot .prod_list {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 30px;
}

.category_hot .prod_list .prodDt_cover img {
  width: 211px;
  height: 312px;
  border-radius: 20px 20px 20px 0;
}

.category_hot .prod_list .prodDt_info span:nth-child(3) {
  display: none;
}

.category_hot .prod_list .prodDt_price p span:nth-child(4) {
  display: none;
}

.breadcrumb_wrap.sps {
  position: fixed;
  left: 0;
  top: 80px;
  width: 100%;
  padding-bottom: 10px;
  border-bottom: 1px solid #d5d5d5;
  background-color: #fff;
  z-index: 100;
}

.breadcrumb_wrap.sps + .contents_wrap {
  padding-top: 100px !important;
}